$main-bk: #FFF
$accent: #486fbb
$accent-dark: #4064ac

.home
  height: 100vh
  width: 100vw
  overflow: hidden
  background: $main-bk


.home-container
  display: block
  width: 500px
  margin: auto
  margin-top: 20vh

  h1
    text-align: center
    margin: 0
    display: block
    font-size: 2.5rem
    margin-bottom: 30px
    color: #424242

  button
    display: block
    margin: auto
    padding: 10px 20px
    border: none
    text-decoration: none
    -webkit-appearance: none
    -moz-appearance: none
    margin-bottom: 12px
    background: #CCC
    color: #444
    min-width: 160px
    border-radius: 2px
    outline: none

    &:hover
      cursor: pointer
      background: #CCC
      transform: scale(1.07)

    &:active
      transform: scale(1.02)

    &:first-of-type
      background: $accent
      color: white
      font-weight: bolder


      &:hover
        background: $accent-dark
