.game
  height: 100vh

.game-actions
  position: absolute
  bottom: 50px
  left: 50px

.action-restart
  display: block
  margin: auto
  padding: 10px 20px
  border: none
  text-decoration: none
  -webkit-appearance: none
  -moz-appearance: none
  margin-bottom: 12px
  background: #CCC
  color: #444
  min-width: 160px
  border-radius: 2px
  outline: none

  &:hover
    cursor: pointer
    background: #AAA

  &:active
    transform: scale(1.03)
