$player-margin: 10px
$card-width: 120px

.player
  position: absolute
  background: #EEE
  border: 1px solid #CCC
  padding: 5px
  border-radius: 7px
  overflow: hidden
  min-width: 300px

  &.player-turn
    border: 1px solid #1565C0
    background: #0D47A1
    color: white

  &.p1
    left: $player-margin
    top: 50vh
    transform: translateY(-50%)


  &.p2
    top: $player-margin
    left: 50vw
    transform: translateX(-50%)

  &.p3
    right: $player-margin
    top: 50vh
    transform: translateY(-50%)

  &.p0
    bottom: $player-margin
    left: 50vw
    transform: translateX(-50%)

.player-hand
  padding: 0px 0px
  margin: 0
  margin-bottom: 0
  padding-bottom: 0

.player-card
  vertical-align: bottom
  width: $card-width
  transform: translateY(0px)
  transition: 0.15s
  border-radius: 7px
  border: 1px solid #DDD

  &:not(:first-child)
    margin-left: -$card-width*0.7

  &.visible
    -webkit-filter: grayscale(0%)
    border-radius: 0
    border: 0

.controllable .player-card:hover
    cursor: pointer
    transition: 0.02s
    transform: translateY(-15px)


.player-header
  position: relative
  padding: 10px 10px
  font-weight: bold

  .dummy
    font-weight: normal

  .declarer
    font-weight: normal

  .bot
    color: #304FFE
    font-weight: 900
    margin-right: 10px
    position: absolute
    right: 0
